import React,{Component} from 'react';
import ReactDOM from 'react-dom';
import { Card, Button, Avatar } from 'antd';
import {Router,Route} from'react-router'
import {Link,HashRouter} from'react-router-dom'
import Item from 'antd/lib/list/Item';
var Data = [{Name:"The Lord of the Rings",Author:"J. R. R. Tolkien",price:"43 $",src:"https://gw.alipayobjects.com/zos/rmsportal/JiqGstEfoWAOHiTxclqi.png"}];
const { Meta } = Card;
const gridStyle = {
  width: '25%',
  textAlign: 'center',
};
export default class Book extends Component{
  constructor(props) {
      super(props);
      this.state={
          detail:this.props.detail
      }  
  }
  
  render(){
    console.log(this.props.detail);
    return(
      <Card.Grid style={gridStyle}>
      <Card
          hoverable={true}
          style={{ width: 300 }}
          cover={<img alt="example" src={this.props.detail.src} />}
          actions={[<form action="/details" method="get"><button type="submit" shape="round" size={'large'}name="key" value={this.props.detail.key} ghost>书籍详细信息</button></form>]}
      >
      <Meta
          title={this.props.detail.Name}
          description={<div><p><b>Author: {this.props.detail.Author}</b></p>
                <p><b>Price: {this.props.detail.price}$</b></p></div>}
      />
      </Card>
     </Card.Grid>
    );
  }
}
