import React,{Component} from 'react';
import ReactDOM from 'react-dom';
import { Card, Button, Avatar,Input } from 'antd';
import Book from'./Book';
import Item from 'antd/lib/list/Item';
var Data = [{key:0,Name:"The Little Prince",Author:"Antoine de Saint Exupéry",price:4,src:"https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1554109928225&di=9032517da01cafd614cfcd15882923fc&imgtype=0&src=http%3A%2F%2Fimg3m4.ddimg.cn%2F14%2F32%2F1388934374-1_w_1.jpg"},
{key:1,Name:"The HoBBIT",Author:"J. R. R. Tolkien",price:20,src:"https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1554109928221&di=f491803d3a494b10f3db9f7a76273368&imgtype=0&src=http%3A%2F%2Fimg3m8.ddimg.cn%2F61%2F20%2F1154741308-1_w_1.jpg"},
{key:2,Name:"River Town",Author:"He Wei",price:43,src:"https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1554109928220&di=4c7830b24dae47be7d92c18d77bb51c7&imgtype=0&src=http%3A%2F%2Fimages.51bi.com%2Fopt%2Fsiteimg%2Fp%2F20141221%2Fe04de35b083fc1caf2e3b85b79bf60b5.jpeg"},
{key:3,Name:"Tuesday with morrie",Author:"Mitch Albom ",price:64,src:"http://img3x5.ddimg.cn/91/29/1487694475-1_w_1.jpg"},
{key:4,Name:"The MARK VERONICA ROTH",Author:"VERONICA ROTH",price:3,src:"https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1554109928220&di=f7113e183e895874c8935e30f4d75fc4&imgtype=0&src=http%3A%2F%2Fimg3m6.ddimg.cn%2F69%2F14%2F1262634486-1_w_1.jpg"},
{key:5,Name:"All the light we cannot see",Author:"Anthony Doerr",price:2,src:"http://img10.360buyimg.com/n1/jfs/t4228/119/3827866920/295510/bc22d2dd/58e8bc32Ndd5172aa.jpg"},
{key:6,Name:"The Tale of two cities",Author:"Charles John Huffam Dickens",price:6,src:"https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1554109928217&di=a4e95ded3d0a1d2b2930ab3e3282fe4e&imgtype=0&src=http%3A%2F%2Fp3.so.qhimgs1.com%2Ft01ebd8d0e2fc25e963.jpg"},
{key:7,Name:"A SEPARATE PEACE",Author:"J. R. R. Tolkien",price:5,src:"https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1554109928216&di=162e383621767823f779c0e77e915792&imgtype=0&src=http%3A%2F%2F5b0988e595225.cdn.sohucs.com%2Fimages%2F20171209%2F20609c5f803e4a5ba89268f28eb46ca2.jpeg"}];
const { Meta } = Card;
const Search = Input.Search;
const gridStyle = {
  width: '25%',
  textAlign: 'center',
};
export default class Cards extends Component{
    constructor(props) {
        super(props);
        this.state={
          data:Data,
          title:"sort",
          value:""
        }  
    }
    inc() {
      var temp = this.state.data.slice();
     // console.log(temp);
      temp.sort(function(a, b) {
        return (a.price<b.price ? -1 : 1);
      });
      //console.log(temp);
      this.setState(() => {
        return { data: temp,
                 title:"递增" }
      })
      console.log(this.state.data);
    }
    dec() {
      var temp = this.state.data.slice();
     // console.log(temp);
      temp.sort(function(a, b) {
        return (a.price<b.price ? 1 : -1);
      });
      //console.log(temp);
      this.setState(() => {
        return { data: temp,
                 title:"递减" }
      })
    }
    handleClick=(value)=>{
      var needle = value.toLowerCase();
      if(value){
      var searchdata = this.state.data.filter((item)=> {
        return item.Name.toString().toLowerCase().indexOf(needle) > -1;
      });
      this.setState({
        data:searchdata,
        value:value})
      }
    }
    First(){
      this.setState({data:Data})
    }
    render(){
      return(
        <div className="Cards-head">
        <p className="parse">Welcome to E-Book</p>
        <div className="cards-header">
        <Button className="cards-header-left" onClick={this.First.bind(this)} >首页</Button>
        <Button className="cards-header-left" onClick={this.inc.bind(this)} >递增</Button>
        <Button className="cards-header-left" onClick={this.dec.bind(this)} >递减</Button>
        </div>
        <Search className="cards-header-search"
      placeholder="书名"
      enterButton="Search"
      size="large"
      onSearch={(value)=>this.handleClick(value)}
    />
        <Card 
        title={<div><p className="inner"><span>书籍列表</span></p></div>}>
            {this.state.data.map((item,key)=>{
              return<Book key={key} detail={item}/>})}
        </Card>
        </div>
      );
    }
}